import Vue from 'vue'
import Vuex from 'vuex'
import data from '@/api/index.js'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        list: []
    },
    getters: {
        getHotels: state => state.list
    },
    mutations: {
        setHotels: (state, items) => {
            state.list = items
            localStorage.setItem('hotels', JSON.stringify(items))
        },
        changeLoading: (state, status) => {
            state.loading = status
        },
        updateList: (state, price) => {
            state.list = [...state.list.filter(h => h.price <= price)]
        },
        setHotel: (state, { id }) => {
            let item = state.list.find(h => h.id === id)
            item.selected = true
        }
    },
    actions: {
        loadData: ({ commit }) => {
            commit('setHotels', data)
        },
        sortable: ({ commit }, price) => {
            commit('updateList', price)
        },
        selectHotel: ({ commit }, item) => {
            commit('setHotel', { id: item.id })
        }
    }
})