<template>
    <div class="booking-table">
        <v-container py-4 px-0>
            <v-layout align-start justify-space-around row>
                <v-flex xs10 lg6 xl6>
                    <v-data-table
                        :headers="headers"
                        :items="hotels"
                    >
                        <template slot="items" scope="props">
                            <td class="text-xs-left"><b>{{ props.item.name }}</b></td>
                            <td class="text-xs-left">${{ props.item.price }}</td>
                            <td class="text-xs-left" v-if="props.item.selected">
                                <b>Текущий выбор</b>
                            </td>
                            <td class="text-xs-left" v-else>
                                <v-btn round small flat outline
                                    :to="{ name: 'Thanks', params: { props } }"
                                    @click="select(props.item)"
                                >Выбрать</v-btn>
                            </td>
                        </template>
                    </v-data-table>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    data () {
        return {
            headers: [
                { text: 'Name', align: 'left', value: 'name' },
                { text: 'Price per/day', align: 'left', value: 'price' },
                { text: 'Selected', align: 'left', value: 'selected' }
            ]
        }
    },
    created () {
        this.$store.dispatch('loadData')
    },
    computed: {
        ...mapGetters({
            hotels: 'getHotels'
        })
    },
    methods: {
        ...mapActions({
            select: 'selectHotel'
        })
    }
}
</script>

<style lang="scss">
.booking-table {
    background: #eaf3fb;

    table { width: 100%; }
}
</style>
