<template>
    <div class="booking-card">
        <v-container py-4>
            <v-layout align-bottom justify-space-around row>
                <v-flex xs8 lg8 xl8>
                    <v-alert
                        :value="true"
                        color="success"
                        icon="check_circle"
                        outline
                    >
                        <p class="subheading font-weight-thin">Спасибо за использование нашего сервиса!</p>
                        <p class="subheading font-weight-thin">Ваше бронирование в <b class="font-weight-bold">{{ hotelName }}</b> подтверждено.</p>     
                    </v-alert>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
export default {
    props: {
        props: {
            item: {
                type: Object,
                required: true
            }
        }
    },
    data () {
        return {
            hotelName: ''
        }
    },
    mounted () {
        if (this.props.item) {
            this.hotelName = this.props.item.name
        }
    }
}
</script>

<style lang="scss">
.booking-card {
    p {
        margin: 0;
    }
}
</style>