import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Thanks from '@/pages/Thanks'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/thanks',
            name: 'Thanks',
            component: Thanks,
            props: true
        }
    ]
})